<template>
    <div class="elementui-demo-item">
        <h3>Notification </h3>
        <el-row>
            <el-col :span="12">
                <el-button-group>
                    <el-button type="primary" @click="open1"></el-button>
                    <el-button type="primary" @click="open2"></el-button>
                    <el-button type="primary" @click="open3"></el-button>
                    <el-button type="primary" @click="open4"></el-button>
                    <el-button type="primary" @click="close1"></el-button>
                </el-button-group>
            </el-col>
            <el-col :span="12">
                <el-button-group>
                    <el-button type="primary" @click="open5"></el-button>
                    <el-button type="primary" @click="open6"></el-button>
                    <el-button type="primary" @click="open7"></el-button>
                    <el-button type="primary" @click="open8"></el-button>
                    <el-button type="primary" @click="close2"></el-button>
                </el-button-group>
            </el-col>
        </el-row>
    </div>
</template>
<script>
// 
/**
 * Options:
 * title: String
 * message: String/VNode
 * dangerouslyUseHTMLString: Boolean
 * type: String
 * iconClass: String
 * customClass: String
 * duration: Number
 * position: top-left/top-right/bottom-right/bottom-left
 * showClose: Boolean
 * onClose: Function
 * onClick: Function
 * offset: Number
 * Methods:
 * close:
 */

// Notification.info, Notification.success, Notification.warning, Notification.error
export default {
    methods: {
        open1 () {
            const h = this.$createElement

            this.$notify({
                type: 'success',
                title: '',
                message: h('i', { style: 'color: teal' }, ''),
                onClose () {
                    console.log('close')
                }
            })
        },
        open2 () {
            this.instance = this.$notify.warning({
                title: '',
                message: '',
                duration: 0,
                offset: 100,
                onClick () {
                    console.log('click')
                }
            })
        },
        open3 () {
            this.$notify.info({
                title: '',
                message: '',
                duration: 1000,
                position: 'top-left',
                showClose: false
            })
        },
        open4 () {
            this.$notify({
                type: 'error',
                title: '',
                message: '',
                position: 'bottom-right'
            })
        },
        close1 () {
            this.instance.close()
        },
        open5 () {
            let vm = this
            vm.noficeLogin = vm.$notify({
                type: 'success',
                title: '',
                message: '!',
                customClass: 'login-notify-class',
                duration: 0,
                position: 'top-right',
                showClose: false,
                offset: 30,
                onClose: function () {
                    vm.$message('nitify')
                },
                onClick: function () {
                    vm.noficeLogin.close()
                }
            })
        },
        open6 () {
            this.$notify({
                type: 'warning',
                title: '',
                message: '<i></i>',
                dangerouslyUseHTMLString: true,
                duration: 2000
            })
        },
        open7 () { },
        open8 () { },
        close2 () {
            this.noficeLogin.close()
        }
    }
}
</script>
<style lang="scss" scoped>
@import "~styles/el-demo-item.scss";
</style>



