<template>
    <div class="elementui-demo-item">
        <h3>Button </h3>
        <el-row>
            <el-col :span="12">
                <i class="el-icon-edit"></i>
                <i class="el-icon-share"></i>
                <i class="el-icon-delete"></i>
                <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
                <el-button-group>
                    <el-button type="primary" icon="el-icon-arrow-left"></el-button>
                    <el-button type="primary">
                        <i class="el-icon-arrow-right el-icon--right"></i>
                    </el-button>
                </el-button-group>
            </el-col>
            <el-col :span="12">
                <el-button-group>
                    <el-button type="primary" icon="el-icon-edit"></el-button>
                    <el-button type="primary" icon="el-icon-share"></el-button>
                    <el-button type="primary" icon="el-icon-delete"></el-button>
                    <el-button type="primary" icon="el-icon-search"></el-button>
                    <el-button type="primary">
                        <i class="el-icon-upload el-icon-right"></i>
                    </el-button>
                </el-button-group>
            </el-col>
        </el-row>
        <el-row style="margin-top: 10px;">
            <el-col :span="12">
                <el-button type="text"></el-button>
                <el-button type="danger" :plain="true" size="mini"></el-button>
                <el-button type="warning" :round="true"></el-button>
            </el-col>
            <el-col :span="12">
                <el-button type="success" disabled size="small"></el-button>
                <el-button type="info" :loading="true" icon="el-icon-loading" size="medium"></el-button>
            </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    methods: {
        search () {
            this.$message({
                message: '',
                type: 'error',
                showClose: true,
                center: true
            })

            // this.$message.info({
            //     message: 'info'
            // })
        }
    }
}
</script>
<style lang="scss" scoped>
@import '~styles/el-demo-item.scss';
</style>



