<template>
    <div class="elementui-demo-item">
        <h3></h3>
        <el-row>
            <el-col>
                <el-button type="text" @click="dialogVisible = true"> Dialog</el-button>
            </el-col>
        </el-row>
        <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="dialogVisible = false"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
export default {
    data () {
        return {
            dialogVisible: false
        }
    },
    methods: {
        handleClose (done) {
            this.$confirm('')
                .then(_ => {
                    done()
                })
                .catch(_ => { })
        }
    }
}
</script>
<style lang="scss" scoped>
@import "~styles/el-demo-item.scss";
</style>



