<template>
    <div class="elementui-demo-item">
        <h3>Alert </h3>
        <el-row>
            <el-col :span="8">
                <el-alert title="" type="success" center :style="{ marginBottom: '10px' }"></el-alert>
                <el-alert title="" type="info" :closable="false" description=" " :style="{ marginBottom: '10px' }"></el-alert>
                <el-alert title="" type="warning" close-text="" @close="handleClose" :style="{ marginBottom: '10px' }"></el-alert>
                <el-alert title="" type="error" show-icon :style="{ marginBottom: '10px' }" center></el-alert>
            </el-col>
        </el-row>
    </div>
</template>
<script>
/**
 * Attributes:
 * title: String
 * type: success/warning/info/error
 * description: String
 * closeable: Boolean
 * center: Boolean
 * close-text: String
 * show-icon: Boolean
 * Events:
 * close
 */
export default {
    methods: {
        handleClose () {
            console.log('alert')
        }
    }
}
</script>
<style lang="scss" scoped>
@import "~styles/el-demo-item.scss";
</style>



